.m-option-base {
  display: inline-block;
  box-sizing: border-box;
  position: relative;
  margin-bottom: 0.5em;
}

input + script + .m-option-base {
  margin-top: 0.5em;
}

.m-option-base:first-child,
.m-option-base:nth-last-of-type(1) {
  margin-bottom: 0;
}

.m-list-optionlist .m-option-base {
  margin-bottom: 0;
  width: 100% !important;
}

.balance .m-option-base {
  flex: 0 0 auto;
  margin-right: 1em;
}